import http from "../utils/http";
export function getList(params) {
  return http.post("/list", params);
}
export function getVote(params) {
  return http.post("http://ok.kiswap.club/api/v1/vote/vote/Vote", params);
}
export function submitVote(params) {
  return http.post("http://ok.kiswap.club/api/v1/vote/vote/GetVote", params);
}